<template>
    <div>
        <!-- user  -->
        <router-link :to="'/user/' + item.id" :key='index' v-for="(item,index) in userList"> {{item.userName}}</router-link>
        <div class="" v-if="userInfo.userName">
            <p> {{userInfo.userName}}</p>
            <p> {{userInfo.sex}}</p>
            <p> {{userInfo.hobby}}</p>
        </div>
        <!--   -->
        <div v-if="userInfo.userName">
            <router-link exact to="?info=follow" >  </router-link>
            <router-link exact to="?info=share" >  </router-link>

            <div>
                {{$route.query}}
            </div>
        </div>
    </div>
</template>

<script>
    //import $ from 'jquery'
    let data = [
      {
        id: 1,
        tip: 'vip',
        userName: 'lishuang',
        sex: '',
        hobby: 'coding'
      },
      {
        id: 2,
        tip: 'vip',
        userName: 'stark',
        sex: '',
        hobby: 'readbook'
      },
      {
        id: 3,
        tip: 'common',
        userName: 'xiaorong',
        sex: '',
        hobby: ''
      }
    ]
    export default {
        data () {
            return {
              userList:data,
              userInfo:{}
            }
        },
        watch:{
            //  created 
            // url 
            $route() {
                this.getData()
            }
        },
        created () {
            // 
            // 
            this.getData()
            this.useJq()
        },
        methods:{
            getData(){
                let id = this.$route.params.userId;
                console.log(this.$route)
                if(id){
                    this.userInfo = this.userList.filter((item)=>{
                        return item.id == id
                    })[0];
                }else{
                    this.userInfo = {}
                }
                console.log(this.userInfo);
            },
            useJq(){
               let html =  $('#stark').val()
                console.log(html)
            }
        }
    }
</script>

<style>
</style>



