//1.
import Vue from 'vue'
import Vuex from 'vuex'

//2.
Vue.use(Vuex)

//3.vuex
let store = new Vuex.Store({
  //
  state:{
    num:100
  },
  //
  mutations:{
    //,state
    addIncrement(state,stark){
      console.log(stark)
      //state
      state.num += stark.n
    },
    minIncrement(state){
      //state
      state.num -= 5
    }
  }

})

//4.store
export default store



